body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.TodoCounter {
    font-size: 24px;
    text-align: center;
}

.title {
    font-size: 16px;
    text-align: center;
    /* Glass */
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 18px );
    -webkit-backdrop-filter: blur( 18px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    /* ---------- */
    width: -webkit-max-content;
    width: max-content;
    margin:  0 auto;
    margin-top: 10px;
    padding: 10px 50px;
    color: rgb(51, 49, 49);
}


.TodoSearch{
    display: flex;
    /* Glass */
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 18px );
    -webkit-backdrop-filter: blur( 18px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    /* ----- */
    width: 400px;
    height: 36px;
    margin:  12px auto;
    text-align: center;
    font-size: 24px;
}

.TodoInputSearch{
    width: 100%;
    height: 100%;
    padding-left: 25px;
    border: none;
    outline: none;
    font-size: 24px;
    color: rgb(51, 49, 49);
    text-align: center;
    background: rgba(26, 26, 26, 0);
}

.TodoButtonSearch{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-max-content;
    height: max-content;
    border: none;
    outline: none;
    margin: auto auto;
    font-size: 24px;
    text-align: right;
    background: rgba( 255, 255, 255, 0 );
}

.TodoButtonSearch:hover {
    /* Rotate 90deg slow*/
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: rotar;
            animation-name: rotar;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes rotar {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        font-size: 24px;
    }
  
    100% {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        font-size: 22px;
    }

  }

@keyframes rotar {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        font-size: 24px;
    }
  
    100% {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        font-size: 22px;
    }

  }
.TodoList{
    margin: 30px 0;
    padding: 0;
}
.TodoItem{
    display: flex;
    align-items: center;
    max-width: 500px;
    margin: 0 auto;
    margin-bottom: 20px;



    background: rgba(226, 226, 226, 0.1);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 16px );
    -webkit-backdrop-filter: blur( 16px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.TodoItem p {
    margin: 0;
    padding: 10px;
    font-size: 24px;
    color: rgb(0, 0, 0);
    text-align: center;
    width: 100%;
}

.textoCompleted{
    text-decoration:line-through;
}

.gancho{
    margin-left: 10px;
    font-size: 24px;
    font-weight: bold;
}

.completed {
    color: green;
}

.borrar{
    position: absolute;
    margin-left: 10px;
    font-size: 20px;
    margin-top: -50px;
    z-index: 1;
    right: 12px;
}

.borrar:hover{
    color: red;
    
    -webkit-animation-duration: 5s;
    
            animation-duration: 5s;
    -webkit-animation-name: girar;
            animation-name: girar;

}

@-webkit-keyframes girar {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        font-size: 24px;
    }
    50% {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
        font-size: 22px;
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
        font-size: 24px;
    }
}

@keyframes girar {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        font-size: 24px;
    }
    50% {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
        font-size: 22px;
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
        font-size: 24px;
    }
}


.CreateTodoButton {
    /* fixed Buttom */
    position: fixed;
    bottom: 10px;
    right: 10px;
    font-size: 24px;
    width: -webkit-max-content;
    width: max-content;

    /* glass */
    background: rgba( 255, 255, 255, 0.55 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 18px );
    -webkit-backdrop-filter: blur( 18px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    /* -------------- */
    border-radius: 12px;
    z-index: 1;
}

.CreateTodoButton:hover {
    box-shadow: 0 8px 32px 0 rgba(255, 255, 255, 0.37);

}
.ModalBackground{
  background: rgba(32, 35, 41, .8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: azure;


}


.TodoForm {
  font-size: 16px;
  text-align: center;
  /* Glass */
  background: rgba(219, 219, 219, 0.87);
  box-shadow: 0 8px 32px 0 rgba(211, 211, 211, 0.76);
  backdrop-filter: blur( 18px );
  -webkit-backdrop-filter: blur( 18px );
  border-radius: 10px;
  border: 1px solid rgba(196, 196, 196, 0.18);
  /* ---------- */
  padding: 50px 50px;
}

.TodoFormLabel {
  font-size: 28px;
  text-align: center;
  margin-top: -10px;
  font-weight: 700;
  color: black;

}

.TodoFormTextarea{
  display: block;
  border: 1px solid rgba(196, 196, 196, 0.18);
  border-radius: 10px;
  padding: 10px;
  font-size: 16px;
  color: rgb(0, 0, 0);
  margin : 2px auto;
  margin-top: 20px;
  width: 100%;
}

.FormButton {
  /* fixed Buttom */
  font-size: 16px;
  width: -webkit-max-content;
  width: max-content;
  margin: 10px 10px 0 10px;
  padding: 2px 20px;

  /* glass */
  background: rgba( 255, 255, 255, 0.55 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 18px );
  -webkit-backdrop-filter: blur( 18px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  /* -------------- */
}

.FormButton:hover {
  box-shadow: 0 8px 32px 0 rgba(255, 255, 255, 0.37);

}

